/* Social Icons in top bar*/
#social-icons-home {
  position: absolute;
  z-index: 200;
  width: 100%;
  margin: 0;
  top: 0; }
  #social-icons-home a:first-child {
    padding-right: 20px; }
  #social-icons-home .social-icon-home {
    font-size: 26px;
    color: #ffffff;
    float: right;
    padding: 5px;
    transition: all .25s ease-in; }
    #social-icons-home .social-icon-home:hover {
      color: #222222; }

/* Social Icons Responsive */
@media (max-width: 600px) {
  #social-icons-home .social-icon-home {
    font-size: 22px;
    padding: 5px; } }
