/* 3: Name  */
#name-animate {
  font-size: 40px;
  text-transform: uppercase;
  letter-spacing: 0.2em;
  font-family: 'Poppins', sans-serif;
  font-weight: 800;
  padding-left: 15px;
  padding-right: 15px;
  margin-bottom: 5px;
  -webkit-filter: drop-shadow(rgba(0, 0, 0, 0.5) 5px 5px 5px);
          filter: drop-shadow(rgba(0, 0, 0, 0.5) 5px 5px 5px); }
  #name-animate .word {
    display: inline-block;
    line-height: 1em; }

/* 4: Description  */
#desc-animate {
  font-weight: 300;
  font-size: 25px;
  font-family: 'Ubuntu', sans-serif;
  margin-bottom: 5px;
  -webkit-filter: drop-shadow(rgba(0, 0, 0, 0.5) 5px 5px 5px);
          filter: drop-shadow(rgba(0, 0, 0, 0.5) 5px 5px 5px); }
  #desc-animate .text-wrapper {
    position: relative;
    display: inline-block;
    padding-top: 0.1em;
    padding-right: 0.05em;
    padding-bottom: 0.15em; }
  #desc-animate .line {
    position: absolute;
    left: 0;
    height: 100%;
    width: 3px;
    background-color: #fff;
    -webkit-transform-origin: 0 50%;
            transform-origin: 0 50%;
    margin-left: 3px;
    opacity: 0; }
  #desc-animate .line1 {
    top: 0;
    left: 0; }
  #desc-animate .letter {
    display: inline-block;
    line-height: 1em; }

