/* Home SVG */
@media (max-width: 1200px) {
  #svgAttributes svg {
    max-width: 325px;
    max-height: 325px; } }

@media (max-width: 900px) {
  #svgAttributes svg {
    max-width: 275px;
    max-height: 275px; } }

@media (max-width: 400px) {
  #svgAttributes svg {
    max-width: 225px;
    max-height: 225px; } }

@media (max-width: 300px) {
  #svgAttributes svg {
    max-width: 150px;
    max-height: 150px; } }

@media (min-width: 250px) and (max-height: 600px) {
  #svgAttributes svg {
    max-width: 150px;
    max-height: 150px; } }

/* Home Title Text */
@media (max-width: 300px) {
  #name-animate {
    font-size: 24px; } }

@media (max-width: 250px) {
  #name-animate {
    font-size: 18px; } }

/* Home Description */
@media (max-width: 300px) {
  #desc-animate {
    font-size: 20px; } }

@media (max-width: 250px) {
  #desc-animate {
    font-size: 14px; } }
