/* Button 1 - Pulse Styles */
.button-pulse {
  font-family: 'Poppins', sans-serif;
  font-weight: 600;
  font-size: 18px;
  background: none;
  border: 3px solid;
  line-height: 1;
  margin: 15px;
  padding: 15px 30px;
  cursor: pointer;
  text-transform: uppercase;
  display: inline-block;
  color: #ffffff;
  border-color: #ffffff;
  transition: 0.25s;
  -webkit-filter: drop-shadow(rgba(0, 0, 0, 0.5) 5px 5px 5px);
          filter: drop-shadow(rgba(0, 0, 0, 0.5) 5px 5px 5px); }
  .button-pulse a {
    text-decoration: none;
    color: inherit; }
  .button-pulse:hover, .button-pulse:focus {
    -webkit-animation: pulse 1s;
            animation: pulse 1s;
    box-shadow: 0 1em 1em 0 rgba(0, 0, 0, 0.4);
    border-color: #ffffff;
    background-color: #ffffff;
    color: #222222; }

/* Button 1- Pulse Animation*/
@-webkit-keyframes pulse {
  0% {
    box-shadow: 0 0 0 1em #8E221D;
    border-color: #ffffff;
    background-color: #ffffff;
    color: #000000; } }
@keyframes pulse {
  0% {
    box-shadow: 0 0 0 1em #8E221D;
    border-color: #ffffff;
    background-color: #ffffff;
    color: #000000; } }

/* Home Button Responsive */
@media (max-width: 300px) {
  .button-pulse {
    border: 2px solid;
    padding: 10px 20px;
    font-size: 16px; } }

@media (max-width: 250px) {
  .button-pulse {
    font-size: 14px;
    padding: 5px 10px; } }
