h1, h2, h3, h4, h5, h6 {
  font-family: "Poppins", sans-serif, "Open Sans";
  font-weight: 800; }

h1 {
  font-size: 35px; }

h2 {
  font-size: 30px; }

h3 {
  font-size: 28px; }

h4 {
  font-size: 24px; }

h5 {
  font-size: 22px; }

h6 {
  font-size: 18px; }

/* General utility classes */
.full-height-width {
  height: 100%;
  width: 100%;
  min-width: 100%; }

.text-center {
  text-align: center; }

.centered-container {
  postion: relative; }
  .centered-container .centered-content {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%); }
